.user-settings-wrapper {
  width: 95%;
  max-width: 900px;
  min-height: 565px;
  position: relative;
}

@media (max-width: 780px) {
  .user-settings-wrapper {
    width: 100%;
  }
}

.user-settings-wrapper::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: black;
  border-radius: 25px;
  z-index: -1;
  opacity: 0;
  -webkit-transition: z-index 1s ease-in-out, opacity .5s ease-in-out;
  transition: z-index 1s ease-in-out, opacity .5s ease-in-out;
}

.user-settings-wrapper.low-z-index::before {
  opacity: .4;
  z-index: 5;
}

.user-settings-block {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 30% 70%;
      grid-template-columns: 30% 70%;
  border-radius: 25px;
  overflow: hidden;
}

@media (max-width: 780px) {
  .user-settings-block {
    -ms-grid-columns: 40% 60%;
        grid-template-columns: 40% 60%;
    height: 100vh;
    border-radius: 0;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}

@media (max-width: 535px) {
  .user-settings-block {
    -ms-grid-columns: 70px calc(100% - 70px);
        grid-template-columns: 70px calc(100% - 70px);
  }
}

.fixed-when-mobile {
  background: #ededed;
  display: -ms-grid;
  display: grid;
  flex-direction: column;
  height: 100%;
  -ms-grid-rows: (1fr)[7];
      grid-template-rows: repeat(7, 1fr);
}

@media (max-width: 535px) {
  .fixed-when-mobile {
    position: fixed;
    width: 70px;
    height: 100vh;
    min-height: 250px;
    -ms-grid-rows: (1fr)[3];
        grid-template-rows: repeat(3, 1fr);
  }
}

.first-text-and-img {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 50px calc(100% - 50px);
      grid-template-columns: 50px calc(100% - 50px);
  align-items: center;
  padding: 18px 10px 18px 25px;
  -webkit-transition: background-color .1s ease-in-out ,color 1s ease-in-out;
  transition: background-color .1s ease-in-out ,color 1s ease-in-out;
  cursor: pointer;
}

.first-text-and-img .img img {
  width: 50px;
  height: 50px;
}

.first-text-and-img.active {
  background: lightgray;
  color: purple;
  font-weight: 700;
}

.first-text-and-img:nth-child(1) {
