.unfolding-content-block {
  padding-left: 10vw;
  margin: 30px auto 30px 0;
}

.unfolding-content-block-main {
  width: 100%;
  max-width: 850px;
  align-items: center;
  justify-content: flex-start;
  display: flex;
}

.unfolding-content-block-main-image {
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
  margin-right: 25px;
}

.unfolding-content-block-main-image img {
  width: 60px;
  cursor: pointer;
}

.unfolding-content-block-main-text {
  text-align: left;
  font-size: 18px;
}

.unfolding-content-block-content {
  max-width: 850px;
  font-style: italic;
  text-align: left;
  font-size: 0;
  margin: 0;
  opacity: 0;
  padding: 0;
  -webkit-transition: opacity .25s, font-size .5s .25s, margin .5s .25s, padding .5s .25s;
  transition: opacity .25s, font-size .5s .25s, margin .5s .25s, padding .5s .25s;
}

.unfolding-content-block-content.shown {
  opacity: 1;
  font-size: 18px;
  padding-left: 85px;
  -webkit-transition: font-size .25s, margin .25s, padding .25s, opacity .5s .25s;
  transition: font-size .25s, margin .25s, padding .25s, opacity .5s .25s;
}

