.user-offer-details-wrapper{max-width:1000px}.user-offer-details{width:95%;max-width:1000px;min-width:300px;grid-template-columns:40% 60%;height:500px;background-color:#fefefe;border-radius:25px;box-sizing:border-box;display:grid;padding:15px;margin:0 auto;align-items:center;justify-content:center;position:relative}.user-offer-details .btn{margin-top:20px;border-radius:5px;height:45px}@media (max-width: 780px){.user-offer-details{border-radius:0;grid-template-columns:100%;width:100%;height:100vh;overflow-y:scroll;overflow-x:hidden}.user-offer-details .btn{margin:10px auto 20px}}@media (max-width: 780px) and (max-width: 500px){.user-offer-details .btn:first-of-type{margin:30px auto 20px}}.user-offer-details-buttons{display:flex;justify-content:space-around}@media (max-width: 500px){.user-offer-details-buttons{flex-direction:column}}.user-offer-details-img img{width:80%;max-width:300px}.user-description-of-item .big-padding{max-height:150px;overflow:auto;text-align:left;padding:15px 25px}@media (max-width: 780px){.user-description-of-item{padding-bottom:35px}}.delete-user-offer-accept{background-color:#5e03fc;color:#fefefe;width:180px;padding:5px 0;margin:10px auto 0;border-radius:5px}.delete-user-offer-accept input{border:none;background-color:transparent;font-size:19px;color:#fefefe;cursor:pointer}.delete-user-offer-accept input:hover{text-decoration:underline}.delete-user-offer-accept input:first-of-type{margin-right:10px}.price-and-url{display:grid;text-align:center;margin-top:15px;font-weight:500;font-size:22px;color:purple;grid-template-columns:50% 50%;gap:10px}.price-and-url .price span{text-decoration:underline;font-weight:600}.price-and-url .scroll-overflow{display:block;width:100%;overflow:auto;font-weight:400;margin-top:8px;font-size:18px}.price-and-url .scroll-overflow:hover{color:black;text-decoration:underline}@media (max-width: 450px){.price-and-url{grid-template-columns:100%;gap:30px}}
