.contact-block{padding:7vw 5vw 0}.contact-block-zero{text-align:left}.contact-block-zero h1{font-weight:600;margin:0}.contact-block-zero h2{font-weight:400}.contact-block-inner{width:100%;display:grid;grid-template-columns:400px calc(100% - 400px)}.contact-block-second{display:flex;flex-wrap:wrap;align-items:center;justify-content:space-around}.contact-block-second-child{width:250px;height:300px;margin:15px}.contact-block-second-child-img{display:flex;align-items:center;justify-content:center;border-radius:10px;border:1px solid gray;width:250px;height:250px}.contact-block-second-child-img img{border-radius:50%;width:210px;height:210px}.contact-block-second-child-text{margin-top:15px;color:purple}.contact-block-second-child-text i{font-size:20px;margin-right:3px;position:relative;top:2px}.contact-block-two-short-inputs{display:grid;grid-template-columns:49% 49%;justify-content:space-between}.contact-block-input,.contact-block-textarea{border-radius:15px;border:none;border:2px solid lightgray;width:100%;height:40px;margin:8px 0;padding:8px 16px;font-size:16px}.contact-block-input::placeholder,.contact-block-textarea::placeholder{color:#a6a6a6}.contact-block-textarea{resize:none;height:160px}.contact-btn{margin-right:auto;color:#fefefe;background-color:#3c009e;display:block;border:none;border-radius:10px;width:110px;font-size:17px;font-weight:600;letter-spacing:.9px;height:40px;cursor:pointer;transition:background-color ease-in .2s, color ease-in .2s}.contact-btn:hover{border:1px solid #3c009e;color:#3c009e;background-color:#fefefe}
